import React, { Dispatch, SetStateAction } from 'react';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { ILog } from '../../interfaces/ILog';
import { IRepository } from '../../interfaces/IRepository';

interface Prop {
    setLogs: Dispatch<SetStateAction<ILog[]>>;
    repo: IRepository;
    setHistoryLimit: Dispatch<SetStateAction<number>>;
    historyLimit: number;
    setMargin: Dispatch<SetStateAction<number>>;
    margin: number
}

export function Settings(prop: Prop) {
    const handleChange_historyLimit = (event: React.ChangeEvent<HTMLInputElement>) => {
        prop.setHistoryLimit(parseInt(event.target.value) || 0);
        prop.repo.setHistoryLimit(parseInt(event.target.value) || 0);
    };
    const handleChange_margin = (event: React.ChangeEvent<HTMLInputElement>) => {
        prop.setMargin(Math.max(parseInt(event.target.value) || 0, 0));
    };
    return (
        <Box display="flex" flexDirection="column" height="100%">
            <Box display="flex" flexDirection="column" flexGrow="1" margin="8px">
                <TextField
                    id="histry limit"
                    variant="outlined"
                    value={prop.historyLimit}
                    onChange={handleChange_historyLimit}
                    label="TaskList Size" />
                <TextField
                    id="standard-basic"
                    variant="outlined"
                    value={prop.margin}
                    onChange={handleChange_margin}
                    label="TaskList Margin(Î²)"
                    style={{ marginTop: "8px" }} />
            </Box>
            <Box display="flex" flexDirection="column" margin="8px">
                <Button color="secondary" variant="contained" onClick={() => {
                    prop.setLogs([])
                    prop.repo.setLogs([])
                }}>delete Logs</Button>
            </Box>
        </Box>
    );
}

export default Settings
